
@charset "UTF-8";
/* =====================

グローバルのスタイル

===================== */
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.global-nav {
  position: fixed;
  top: 0;
  right: 0;
  margin: 20px 40px;
}
.global-nav ul {
    padding: 0;
    text-align: right;
    list-style: none;
}
.global-nav ul a {
      text-decoration: none;
      color: #35495e;
      font-weight: bold;
      -webkit-transition: 0.3s;
      transition: 0.3s;
}
.global-nav ul a:hover {
        opacity: 0.6;
}
.fade-enter-active {
  -webkit-transition: opacity 2s;
  transition: opacity 2s;
}
.fade-enter,
.fade-leave {
  opacity: 0;
}
body {
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}
@media (max-width: 480px) {
body {
    background: #ccc;
}
}

.sub-nav {
  position: absolute;
  right: 0;
  margin: 20px 40px;
}
.sub-nav ul {
    padding: 0;
    text-align: right;
    list-style: none;
}
.sub-nav ul a {
      text-decoration: none;
      color: #35495e;
      font-weight: bold;
      -webkit-transition: 0.3s;
      transition: 0.3s;
}
.sub-nav ul a:hover {
        opacity: 0.6;
}
